import { useEffect, useState } from "react"

const useTheme = () => {
  const [theme, setTheme] = useState('light')

  useEffect(() => {
    
  }, [theme])

  return {
    theme,
    setTheme,
    toggle() {
      setTheme(theme === 'light' ? 'dark' : 'light')
    }
  }
}

export default useTheme
