import Button from "./Button"
import cv from 'assets/Daler-CV.pdf'
import Tilt from 'react-tilt'
import SectionTitle from "./SectionTitle"


const About = () => {
  return (
    <section className={'section about'} id={'about'}>
      
      <SectionTitle lg={'About'} sm={'My introduction'} />

      <div className={'about__body'}>
        <div className={'about__img-container'}>
          <Tilt options={{ max: 20, scale: 1 }}>
            <img
              className={'about__img'}
              src={'https://picsum.photos/id/1/700/400'}
              alt={'Wait'}
            />
          </Tilt>
        </div>
        <div className={'about__main'}>
          <p className={'about__text'}>
            Hello, my name is Saiodov Daler, and I am
            professional web developer. I have experience
            in working with laterset web tecknologies, including
            React, Vue and Node Js
          </p>
          <ul className={'about__info-items'}>
            <li className={'about__info-item'}>
              <div className={'about__info-item-num'}>
                1.5+
              </div>
              <div className={'about__info-item-desc'}>
                {`Years of
                experiece`}
              </div>
            </li>
            <li className={'about__info-item'}>
              <div className={'about__info-item-num'}>
                10+
              </div>
              <div className={'about__info-item-desc'}>
                {`Real life
                  projects`}
              </div>
            </li>
            <li className={'about__info-item'}>
              <div className={'about__info-item-num'}>
                10+
              </div>
              <div className={'about__info-item-desc'}>
                {`Happy
                  clients`}
              </div>
            </li>
          </ul>
          <a href={cv} download={'Daler-CV.pdf'}>
            <Button
              color={'primary'}
              Icon={'uil uil-import'}
              children={'Download CV'}
            />
          </a>
        </div>
      </div>

    </section>
  )
}

export default About
